.glass-card-container{
    min-height: 100vh;
    background: linear-gradient(to bottom,
    rgb(194, 164, 194),
    rgb(255, 215, 255));
    overflow: hidden;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

@mixin circle($top, $left, $size) {
    position: absolute;
    top: $top;
    left: $left;
    height: $size;
    width: $size;
    border-radius: 50%;
    background: linear-gradient(to bottom right,
                rgb(252, 222, 250),
                rgb(230, 192, 230),
                rgb(218, 166, 218));
    // border: 1px solid rgb(255, 215, 255);
    border-bottom: none;
    box-shadow: 0 0 100px rgba(234, 0, 255, 0.15)
}

.circle1 {
    @include circle(-70px, 300px, 120px);
}

.circle2 {
    @include circle(25px, -100px, 150px);
}

.circle3 {
    @include circle(-20px, 100px, 80px);
}

.circle4 {
    @include circle(60px, 200px, 80px);
}

.circle5 {
    @include circle(100px, 320px, 70px);
}

.circle6 {
    @include circle(210px, 20px, 100px);
}

.blur1 {
    @include circle(-100px, -50px, 500px);
    background: linear-gradient(to bottom right,
                rgb(252, 246, 222),
                rgb(230, 224, 192),
                rgb(218, 206, 166));
    filter: blur(80px);
}

.blur2 {
    @include circle(-50px, 300px, 500px);
    background: linear-gradient(to bottom right,
                rgb(249, 222, 252),
                rgb(229, 192, 230),
                rgb(218, 166, 214));
    filter: blur(80px);
}

.blur3 {
    @include circle(300px, 150px, 500px);
    background: linear-gradient(to bottom right,
                rgb(236, 222, 252),
                rgb(213, 192, 230),
                rgb(197, 166, 218));
    filter: blur(80px);
}

.glass-card-background{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0.6);
    width: 350px;
    height: 200px;
    border-radius: 20px;
    @media screen and (min-width: 350px) {
        transform: translate(-50%, -50%) scale(0.7);
    }
    @media screen and (min-width: 420px) {
        transform: translate(-50%, -50%) scale(0.8);
    }
    @media screen and (min-width: 520px) {
        transform: translate(-50%, -50%) scale(1);
    }
    @media screen and (min-width: 720px) {
        transform: translate(-50%, -50%) scale(1.2);
    }
    @media screen and (min-width: 850px) {
        transform: translate(-50%, -50%) scale(1.5);
    }
}

.glass-card{
    position: relative;
    width: 350px;
    height: 200px;
    border-radius: 20px;
    z-index: 1;
    transform: scale(0.6);
    @media screen and (min-width: 350px) {
        transform: scale(0.7);
    }
    @media screen and (min-width: 420px) {
        transform: scale(0.8);
    }
    @media screen and (min-width: 520px) {
        transform: scale(1);
    }
    @media screen and (min-width: 720px) {
        transform: scale(1.2);
    }
    @media screen and (min-width: 850px) {
        transform: scale(1.5);
    }
}

.upper-glass{
    position: relative;
    height: 120px;
    overflow: hidden;
    border-radius: 20px 20px 0 0;
}

.border{
    position: absolute;
    right: -100px;
    top: -100px;
    height: 220px;
    width: 220px;
    border-radius: 50%;
    border: 1px solid white;
}

.upper-blur{
    position: absolute;
    height: 100%;
    width: 100%;
    background: rgba(255, 255, 255, 0.1);
    filter: blur(10px);
    backdrop-filter: blur(5px);
    z-index: -1;
}

.upper-text{
    height: 120px;
    padding: 15px 20px;
    box-sizing: border-box;
    text-transform: uppercase;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 700;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.card-name{
    p{
        color: rgb(176, 131, 187);
    }
}

.card-number{
    p{
        color: rgb(19, 62, 133);
    }
}

.lower-text{
    height: 80px;
    padding: 15px 20px;
    box-sizing: border-box;
    text-transform: uppercase;
    font-weight: 300;
    letter-spacing: 0.8px;
    color: white;
}

.valid-name{
    height: 6px;
    p{
        font-size: 6px;
    }
}

.valid-date{
    height: 16px;
    p{
        font-size: 16px;
    }
}

.user-name{
    margin: 9px 0;
}

.logo-img{
    position: absolute;
    right: 30px;
    top: 30px;
    img{
        width: 64px;
        height: 64px;
    }
}

.lower-glass{
    position: relative;
    height: 80px;
    background: linear-gradient(to right,
    rgb(88, 166, 212),
    rgb(62, 134, 232),
    rgb(185, 66, 234));
    border-radius: 0 0 20px 20px;
}

.lower-circles{
    position: absolute;
    bottom: 15px;
    right: 20px;
    height: 20px;
    width: 80px;
}

.logo{
    position: absolute;
    top: 0;
    height: 20px;
    width: 20px;
    border-radius: 50%;
}

.logo1{
    left: 16px;
    background: rgba(255, 255, 255, 0.15);
}

.logo2{
    right: 16px;
    background: rgba(255, 255, 255, 0.05);
}

.logo3{
    left: 30px;
    background: rgba(255, 255, 255, 0.1);
}

.design-name{
    position: absolute;
    bottom: -75px;
    left: 50%;
    transform: translateX(-50%);
    color: rgba(255, 255, 255, 1);
    text-transform: uppercase;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 900;
    font-size: 40px;
    width: 500px;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    backdrop-filter: blur(5px);
    background: rgba(255, 255, 255, 0.1);
    padding: 0 20px;
}